import React,{useState,createContext} from 'react'
import {data} from './data'
import InstagramIcon from '@material-ui/icons/Instagram';
import './App.css'
export function Component(props) {
    const [cart,setcart]=useState([]);
    //..................
    const adder=(data)=>{
      setcart([...cart,data]);
    }
    //...............


 
    return (
        <div className="component">
            
           <div className="compo">
               <h1>Shop</h1>
           {
               data.map((data)=>{
                   return(
                       <div key={data.id} className="compodiv">
                           <h1>{data.dish}</h1>
                           <img className="img-compo" src={data.img}></img>
                           <h1>	&#8377;{data.Rs}</h1>
                           <button className="btn" onClick={()=>{adder(data)}}> Add to cart</button>
                           </div>
                   )
               })
           } 
           </div>
           <div className="Cart">
               <h1>MyCart</h1>
           {
               cart.map((i)=>{
                   return(
                       <div key={i.id} className="cartdiv">
                           <h1>{i.dish}</h1>
                           <img className="img-compo" src={i.img}></img>
                           <h1>	&#8377;{i.Rs}</h1>
                           </div>
                   )
               })
           }
           </div>

        </div>
    )
}


