import React from 'react'
import {Link} from 'react-router-dom'
function Header() {
    return (
        <div className="header">
            <Link to="/login"><img className="img-login" src="data:image/jpeg;base64,"></img></Link>
            <Link to="/"><img className="img-logo" src="https://i.pinimg.com/600x315/0e/cb/2d/0ecb2d69ff5acf4cda38a3ef8a0c93bc.jpg"></img></Link>
            <Link to="/info"><img className="img-about" src="https://c0.klipartz.com/pngpicture/257/702/gratis-png-acerca-de-nosotros-logotipo-logotipo-de-la-empresa-servicio-de-marca-de-la-empresa-icono-acerca-de-nosotros-hd.png"></img></Link>
        </div>
    )
}

export default Header
